* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #3cdbc0;
  font-size: 16px;
}

ul {
  padding: 0;
}/* HEADER */.container {
  display: flex;
  flex-flow: column nowrap;
  font-family: "Rubik", sans-serif;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  background: white;
}

.logo {
  width: 155px;
  padding-top: 10px;
  text-align: center;
}

.menu {
  margin: 0;
}

.item {
  display: inline-block;
}

.item .link {
  padding: 10px 10px 13px 10px;
  text-decoration: none;
  color: #4a4a4a;
}/* MAIN */.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  min-height: 250px;
  padding-top: 105px;
  color: #ffffff;
  text-align: center;
}

.title {
  width: 230px;
  font-size: 30px;
}

@media all and (min-width: 760px) {
  .title {
    font-size: 40px;
  }
}

@media all and (min-width: 768px) {
  .header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }

  .logo {
    padding: 20px;
    padding-right: 30px;
  }

  .menu {
    padding-left: 30px;
  }
}
